import clsx from "clsx"
import {Children, FC, HTMLAttributes, useCallback, useRef, useState} from "react"
import ChevronRightIcon from "../../../icons/ChevroRightIcon"
import ChevronLeftIcon from "../../../icons/ChevronLeftIcon"
import DotsSelect from "../DotsSelect/DotsSelect"
import "./SlideShow.css"

type SlideShowProps = HTMLAttributes<HTMLDivElement> & {}

const SlideShow: FC<SlideShowProps> = props => {
    const {children, className, ...otherProps} = props
    const slides = Children.toArray(children)
    const [currentSlideIndex, setCurrentSlideIndex] = useState(0)
    const [dragStart, setDragStart] = useState<number>()
    const [isDragging, setIsDragging] = useState(false)
    const containerRef = useRef<HTMLDivElement>(null)

    const goToSlide = useCallback(
        (index: number) => {
            const currentSlideElement = containerRef.current?.children[index]
            if (currentSlideElement) {
                currentSlideElement.scrollIntoView({behavior: "smooth", block: "nearest", inline: "center"})
            }
        },
        [containerRef],
    )

    const goNext = useCallback(() => {
        const next = currentSlideIndex < slides.length - 1 ? currentSlideIndex + 1 : 0
        goToSlide(next)
        setCurrentSlideIndex(next)
    }, [currentSlideIndex, setCurrentSlideIndex, goToSlide, slides])

    const goPrev = useCallback(() => {
        const prev = currentSlideIndex > 0 ? currentSlideIndex - 1 : slides.length - 1
        goToSlide(prev)
        setCurrentSlideIndex(prev)
    }, [currentSlideIndex, setCurrentSlideIndex, goToSlide, slides])

    const handleDotClick = useCallback(
        (index: number) => {
            setCurrentSlideIndex(index)
            goToSlide(index)
        },
        [setCurrentSlideIndex, goToSlide],
    )

    const handleTouchStart = useCallback(
        (event: React.TouchEvent<HTMLDivElement>) => {
            setDragStart(event.touches[0]?.clientX)
        },
        [setDragStart],
    )

    const handleTouchMove = useCallback(
        (event: React.TouchEvent<HTMLDivElement>) => {
            event.preventDefault()
            if (isDragging) return
            console.log("handleTouchMove", `isDragging ${isDragging}`)
            const dragCurrentPos = event.touches[0]?.clientX
            if (!dragCurrentPos || !dragStart) return
            const diff = dragCurrentPos - dragStart
            if (Math.abs(diff) < 10) return
            setIsDragging(true)
            if (diff < 0) {
                goNext()
            } else {
                goPrev()
            }
        },
        [dragStart, goNext, goPrev, isDragging],
    )

    const handleTouchEnd = useCallback(
        (event: React.TouchEvent<HTMLDivElement>) => {
            setIsDragging(false)
        },
        [setIsDragging],
    )

    return (
        <div
            className={clsx("slideshow-73jc", className)}
            onTouchStart={handleTouchStart}
            onTouchMove={handleTouchMove}
            onTouchEnd={handleTouchEnd}
            {...otherProps}
        >
            <div className="container-73jc" ref={containerRef}>
                {slides.map((slide, index) => (
                    <div className="slide" key={index}>
                        {slide}
                    </div>
                ))}
            </div>
            <button className="prev-button" onClick={goPrev}>
                <ChevronLeftIcon className="icon" />
            </button>
            <button className="next-button" onClick={goNext}>
                <ChevronRightIcon className="icon" />
            </button>
            <DotsSelect
                className="dots"
                length={slides.length}
                selected={currentSlideIndex}
                onSelect={handleDotClick}
            />
        </div>
    )
}

export default SlideShow
